/* --------- */
/*  Preloader */
/* --------- */
#preloader {
 position: fixed;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 z-index: 999999;
 background-color: #fff;
 background-image: url(/static/media/pre.f4cbbb8f.svg);
 background-repeat: no-repeat;
 background-position: center;
}

#preloader-none {
 opacity: 0;
}

#no-scroll {
 overflow: hidden;
 height: 100vh;
}

/* --------- */
/*Scrollbar   */
/* --------- */

::-webkit-scrollbar {
 width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
 background: #F8F8F8;
}

/* Handle */
::-webkit-scrollbar-thumb {
 background: #3bbec7;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
 background: #25A4AD;
}
